<template>
  <v-container>
    <v-row><v-col class="font-weight-bold text-h6 pb-0" v-show="!overlay">간편 회원가입</v-col></v-row>

    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>

    <v-row class="pa-3 mt-0" v-show="!overlay">
      <v-col>
        <v-form ref="kakaoform" lazy-validation>
          <v-text-field label="이름" v-model="member.name" :rules="nameRule"></v-text-field>
          <v-text-field label="생년월일" v-model="member.birth" type="date" :rules="birthRule"></v-text-field>
          <v-text-field label="전화번호" :rules="phoneRule" v-model="member.phone"></v-text-field>
          <v-text-field label="이메일" v-model="member.email" :rules="emailRule"></v-text-field>
          <v-container>
            <v-row>
              <v-col align="left">
                <v-btn class="mr-4" @click="validJoin" color="primary" medium> 가입하기 </v-btn>
              </v-col>
              <v-col align="right">
                <v-btn @click="clear" class="red white--text"> 초기화 </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-form></v-col
      >
    </v-row>
  </v-container>
</template>

<script>
import memberApi from "@/apis/member";
export default {
  name: "KakaoJoin",
  data: () => ({
    member: {
      id: "",
      password: "",
      name: "",
      birth: "",
      phone: "",
      email: "",
    },
    overlay: true,
    token: "",
    phoneRule: [
      (value) => {
        const pattern = /^(01)[0-9]{1}-[0-9]{3,4}-[0-9]{4}$/;
        return pattern.test(value) || "핸드폰 번호 형식이 아닙니다.";
      },
    ],
    emailRule: [(value) => String(value).length > 0 || "필수 요소입니다.", (value) => /.+@.+\..+/.test(value) || "이메일 형식이 아닙니다."],
    birthRule: [(value) => String(value).length > 9 || "필수 요소입니다."],
    nameRule: [(value) => String(value).length > 0 || "필수 요소입니다."],
  }),
  methods: {
    clear() {
      this.member.name = "";
      this.member.birth = "";
      this.member.phone = "";
      this.member.email = "";
    },

    async validJoin() {
      if (this.$refs.kakaoform.validate()) {
        try {
          let result = await memberApi.joinKaKao(this.member, this.access_token);
          if (result.data.id) {
            this.$router.replace("/member/login");
          }
        } catch (error) {
          console.log("error");
        }
      }
    },
    async getKToken(code) {
      try {
        const promise = await memberApi.getKakaoToken(code);
        console.log(promise.data);
        this.access_token = promise.data.access_token;
        this.member.id = promise.data.id;
        this.member.password = promise.data.id;

        const loginPromise = await memberApi.login(this.member);
        if (loginPromise.data.result === "success") {
          this.$store.dispatch("saveAuth", {
            id: loginPromise.data.id,
            jwt: loginPromise.data.jwt,
            mno: loginPromise.data.mno,
            gname: loginPromise.data.gname,
            mileagepoint: loginPromise.data.mileage,
            kToken: this.access_token,
          });
          this.$router.replace("/home/main");
        }

        this.overlay = false;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getKToken(this.$route.query.code);
  },
};
</script>

<style></style>
